.gameBoard {
  height: 680px;
  width: 700px;
  background-color: #e4a3a3;
  border: 1px solid gray;
  border-radius: 7px;
  padding: 0px 7px 0px 7px;
  box-shadow: 14px 8px 16px 4px #8d4c4c;
}

.gameBoard table {
  width: 100%;
  height: 100%;
}

.gameBoard tr:nth-child(2) td {
  border-radius: 7px;
  box-shadow: 0px 0px 18px 11px #8b4c4c;
}

.gameBoard tr:nth-child(1) td:nth-child(1),
.gameBoard tr:nth-child(1) td:nth-child(3),
.gameBoard tr:nth-child(3) td:nth-child(1),
.gameBoard tr:nth-child(3) td:nth-child(3) {
  height: 45%;
  width: 48%;
}

.gameBoard tr:nth-child(1) td:nth-child(2),
.gameBoard tr:nth-child(3) td:nth-child(2) {
  
}
